export const GetDataProfilesDocument = gql`
    query getDataProfiles($urn: String!, $limit: Int, $startTime: Long, $endTime: Long) {
        dataset(urn: $urn) {
            urn
            datasetProfiles(limit: $limit, startTimeMillis: $startTime, endTimeMillis: $endTime) {
                rowCount
                columnCount
                timestampMillis
                fieldProfiles {
                    fieldPath
                    uniqueCount
                    uniqueProportion
                    nullCount
                    nullProportion
                    min
                    max
                    mean
                    median
                    stdev
                    sampleValues
                }
            }
        }
    }
`;

