export const GetDatasetDocument = gql`
    query getDataset($urn: String!) {
        dataset(urn: $urn) {
            ...nonRecursiveDatasetFields
            schemaMetadata(version: 0) {
                ...schemaMetadataFields
            }
            previousSchemaMetadata: schemaMetadata(version: -1) {
                ...schemaMetadataFields
            }
            editableSchemaMetadata {
                editableSchemaFieldInfo {
                    fieldPath
                    description
                    globalTags {
                        ...globalTagsFields
                    }
                    glossaryTerms {
                        ...glossaryTerms
                    }
                }
            }
            deprecation {
                actor
                deprecated
                note
                decommissionTime
            }
            globalTags {
                ...globalTagsFields
            }
            glossaryTerms {
                ...glossaryTerms
            }
            subTypes {
                typeNames
            }
            domain {
                ...entityDomain
            }
            container {
                ...entityContainer
            }
            usageStats(resource: $urn, range: MONTH) {
                buckets {
                    bucket
                    duration
                    resource
                    metrics {
                        uniqueUserCount
                        totalSqlQueries
                        topSqlQueries
                    }
                }
                aggregations {
                    uniqueUserCount
                    totalSqlQueries
                    users {
                        user {
                            urn
                            username
                        }
                        count
                        userEmail
                    }
                    fields {
                        fieldName
                        count
                    }
                }
            }
            datasetProfiles(limit: 1) {
                rowCount
                columnCount
                timestampMillis
                fieldProfiles {
                    fieldPath
                    uniqueCount
                    uniqueProportion
                    nullCount
                    nullProportion
                    min
                    max
                    mean
                    median
                    stdev
                    sampleValues
                }
            }
            health {
                status
                message
                causes
            }
            assertions(start: 0, count: 1) {
                total
            }
            operations(limit: 1) {
                timestampMillis
            }
            upstream: lineage(input: { direction: UPSTREAM, start: 0, count: 100 }) {
                ...fullLineageResults
            }
            downstream: lineage(input: { direction: DOWNSTREAM, start: 0, count: 100 }) {
                ...fullLineageResults
            }
            ...viewProperties
            autoRenderAspects: aspects(input: { autoRenderOnly: true }) {
                aspectName
                payload
                renderSpec {
                    displayType
                    displayName
                    key
                }
            }
            status {
                removed
            }
            readRuns: runs(start: 0, count: 20, direction: INCOMING) {
                count
                start
                total
            }
            writeRuns: runs(start: 0, count: 20, direction: OUTGOING) {
                count
                start
                total
            }
        }
    }
    ${NonRecursiveDatasetFieldsFragmentDoc}
    ${SchemaMetadataFieldsFragmentDoc}
    ${GlobalTagsFieldsFragmentDoc}
    ${GlossaryTermsFragmentDoc}
    ${EntityDomainFragmentDoc}
    ${EntityContainerFragmentDoc}
    ${FullLineageResultsFragmentDoc}
    ${ViewPropertiesFragmentDoc}
`;

